.v2ex-card {
  color: #666;
  font-size: 13px;
  position: fixed;
  padding-top: 10px;
  width: 280px;
  height: 200px;
  left: 0;
  top: 0; }
  .v2ex-card *,
  .v2ex-card *::before,
  .v2ex-card *::after {
    box-sizing: border-box; }
  .v2ex-card .vc-inner {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 1px solid #e1e4e8;
    border-radius: 5px;
    box-shadow: 0 8px 10px rgba(149, 157, 165, 0.2); }
    .v2ex-card .vc-inner:before {
      top: -16px;
      right: 131px;
      left: auto;
      position: absolute;
      display: inline-block;
      content: '';
      border: 8px solid transparent;
      border-bottom-color: rgba(27, 31, 35, 0.15); }
    .v2ex-card .vc-inner:after {
      top: -14px;
      right: 132px;
      left: auto;
      border: 7px solid transparent;
      border-bottom-color: #fff;
      position: absolute;
      display: inline-block;
      content: ''; }
    .v2ex-card .vc-inner .vc-header {
      display: flex;
      padding: 8px;
      border-bottom: 1px solid #e1e4e8; }
      .v2ex-card .vc-inner .vc-header .vc-avatar {
        height: 48px; }
        .v2ex-card .vc-inner .vc-header .vc-avatar img {
          border-radius: 4px; }
      .v2ex-card .vc-inner .vc-header .vc-info {
        flex: 1;
        line-height: 1;
        padding-left: 8px;
        color: rgba(0, 0, 0, 0.5); }
        .v2ex-card .vc-inner .vc-header .vc-info .vc-row {
          margin-bottom: 5px; }
          .v2ex-card .vc-inner .vc-header .vc-info .vc-row:last-child {
            margin-bottom: 0; }
        .v2ex-card .vc-inner .vc-header .vc-info .vc-name {
          color: #555;
          font-weight: bold; }
        .v2ex-card .vc-inner .vc-header .vc-info .vc-online {
          padding: 1px 4px;
          border-radius: 3px;
          margin-left: 5px;
          color: #fff;
          background-color: #52bf1c; }
        .v2ex-card .vc-inner .vc-header .vc-info .vc-activity-rank {
          margin-left: 5px; }
  .v2ex-card.vc-hide {
    display: none; }
